@import "src/styles/global.scss";
.swiper-pagination-bullet {
  background-color: $color-bg;
  opacity: 1;
}

.swiper-pagination-bullet-active {
  background-color: $color-bg-accent;
}

.swiper-pagination {
  bottom: 24px !important;
}

.swiper {
  &__link {
    position: relative;
    display: flex;
  }

  &__img {
    width: 100%;
    -o-object-fit: contain;
       object-fit: contain;

    &--good {
      position: absolute;
      inset: 0;
    }
  }
}
